<!-- This page is set to use the ProductsGrid and ProducstsGridItem.vue component pages -->
<!-- bind :products to pass products property to the ProductsGrid component -->
<!-- products data is imported on this page and can be used to pass to other components-->
<!-- import ProductsGrid component into this page under script section, and register it as an export component -->
<!-- once registered, ProductsGrid can then be used as a tag <ProductsGrid> in the template section -->

<template>
    <div id="page-wrap">
      <ProductsGrid :products="products" />
    </div>
</template>

<script>

// Import products array from fake-data.json
import axios from 'axios';
import ProductsGrid from '../components/ProductsGrid.vue';

// Export allows other components to import this component and display it
export default {
    name: 'ProductsPage',
    components: {
      ProductsGrid,
    },
    data() {
        return {
            products: [], // set products to empty array for inital state
        };    
    },
    async created() {   // the 'created' lifecycle method used to initialize product data when page is first loaded. Called at specific point in process of rendering a component. 
      const result = await axios.get('/api/products'); // can use relative path now that the proxy is setup to the backend server in view.config.js
      const products = result.data;
      this.products = products;   // set the products empty array from the component above to the data loaded here from the server through axios connection
    }
};
</script>