<!-- use v-for directive to loop through product items, bind with key of product id -->
<!-- pass product prop with a :bind command of product=product to pass the product prop down to child components -->
<!-- import ProductsGridItems for individual products inside the script section, register in the export default by components property -->
<!-- add a props property of 'products' so the products can be passed from the ProductPage.vue -->
<!-- This way, items in the product grid can be customized, not the same all the time, makes it reusable on other pages if needed -->

<template>
    <div class="grid-wrap">
        <ProductsGridItem
            v-for="product in products"
            :key="product.id"
            :product="product" />
    </div>
</template>

<script>
import ProductsGridItem from './ProductsGridItem.vue';
export default {
    name: 'ProductsGrid',
    props: ['products'],
    components: {
        ProductsGridItem,
    },
}
</script>

<style scoped>
  .grid-wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 16px;
  }

</style>