<!-- use props to pass data from parent components to open slots in your code -->
<!-- props allows the decoupling of component data from the components themselves -->
<!-- create props as an attribute of default export object, an array of properties the current component can accept -->
<!-- Parent component can then pass down values to these props -->

<template>
<div class="product-item"> 
    <img v-bind:src="product.imageUrl" />
    <h3 class="product-name">{{ product.name }}</h3>
    <p class="product-price">${{ product.price }}</p>
    <router-link v-bind:to="'/products/' + product.id"> <!-- router-link tag allows navigation between pages without full page refresh -->
        <button>View Details</button>
    </router-link>
</div>
</template>

<script>
export default {
    name: 'ProductsGridItem',
    props: ['product'],
}
</script>

<style scoped>
  .product-item {
    align-items: center;
    border-radius: 8px;
    box-shadow: 0px 2px 5px #888;
    display: flex;
    flex-direction: column;
    margin-bottom: 2%;
    padding: 20px;
    position: relative;
    width: 32%;
  }

  .product-name {
    margin-bottom: 0;
  }

  img {
    height: 200px;
    width: 200px;
  }

  a {
    width: 100%;
  }

  button {
    width: 100%;
  }
</style>