<template>
    <!-- loop through cartItems array with v-for and bind with v-bind:key or simply :key, same with v-bind:src, simply say :src -->
    <div class="product-container">
    <img class="product-image" :src="product.imageUrl" />
    <div class="details-wrap">
        <h3>{{ product.name }}</h3>
        <p>${{ product.price }}</p>
    </div>
    <!-- button needs its own "$emit" function native to vue in order to have components emit their own events that can be used elsewhere  -->
    <!-- $emit takes two arguments, first is name of the event which can be whatever, 2nd is the data you want to pass along with the event -->
    <button 
      class="remove-button"
      v-on:click="$emit('remove-from-cart', product.id)"
    >Remove From Cart</button>
    </div>
</template>

<script>
export default {
    name: 'ProductsListItem',
    props: ['product'],
}
</script>

<style scoped>
.product-container {
    align-content: 'center';
    border-bottom: 1px solid #ddd;
    display: flex;
    padding: 16px;
    width: 100%;
  }

  .product-image {
    flex: 1;
    height: 100px;
    max-width: 100px;
  }

  .details-wrap {
    padding: 0 16px;
    flex: 3;
  }

  .remove-button {
    flex: 1;
    margin: auto;
  }
  </style>