<!-- create a ProductsListItem custom tag and loop over the products array with the v-for, key bind directive -->
<!-- also pass the :product property through with the v-bind directive  -->
<!-- register the ProductsListItem component in the exports section, also need to import it -->
<!-- export props products array from the Carts page -->

<template> 
    <!-- wrap products list in v-if directive to check if product items exist, if so display, if not skip -->
    <div v-if="products.length > 0">
    <!-- v-on event listens for 'remove-from-art' even from the ProductsListItem component, and when it occurs, emits its own remove from cart event -->
    <!-- v-on emit takes two arguments, name of the event, and secondly the $event param which was passed from ProducListItem, in this case the productId -->
        <ProductsListItem
            v-for="product in products"
            v-on:remove-from-cart="$emit('remove-from-cart', $event)"
            :key="product.id"
            :product="product" />
    </div>
    <p v-else>You haven't added anything to your cart yet!</p>
</template>

<script>
import ProductsListItem from './ProductsListItem.vue';

export default {
    name: 'ProductsList',
    props: ['products'],
    components: {
        ProductsListItem,
    },
}
</script>