<template>
<!-- need a v-if directive on the outer div so if the product exists, it displays product page, otherwise page not found -->
<!-- if the v-if directive evaluates to TRUE, then it will display everything inside the outer div, if not skips down to next command in the block, NotFound v-else -->
<!-- Remember to import the NotFoundPage.vue in the script import statements below and pass component property to default export object -->
    <div id="page-wrap" v-if="product">
      <div id="img-wrap">
        <img v-bind:src="product.imageUrl" />
      </div>
      <div id="product-details">
        <h1>{{ product.name }}</h1>
        <h3 id="price">${{ product.price }}</h3>
        <p>Average rating: {{ product.avergageRating }}</p>
        <button 
          id="add-to-cart"
          v-if="!itemIsInCart && !showSuccessMessage" 
          v-on:click="addToCart"
        >Add to Cart</button>   <!-- bind methods to elements with v-on, method addToCart defined below -->
        <button 
          id="add-to-cart"
          class="green-button"
          v-if="!itemIsInCart && showSuccessMessage" 
        >Successfully added to cart</button>   <!-- bind methods to elements with v-on, method addToCart defined below -->
        <button 
          id="add-to-cart"
          class="grey-button"
          v-if="itemIsInCart" 
        >Item is already in cart</button>   <!-- bind methods to elements with v-on, method addToCart defined below -->
        <h4>Description</h4>
        <p>{{ product.description}}</p>
      </div>
    </div>
    <NotFoundPage v-else/>
</template>

<script>
// Import products array from fake-data.json
import axios from 'axios';
import NotFoundPage from './NotFoundPage';

// Export allows other components to import this component and display it
export default {
    name: 'ProductsDetailPage',
    components: {
      NotFoundPage,
    },
    data() {
      return {
        product: {},
        cartItems: [],
        showSuccessMessage: false, // flag for signaling if item has been added to cart
      };
    },
    computed: {   // create computed property to check whether current product Id is already in the users cart
      itemIsInCart() {
        return this.cartItems.some(item => item.id === this.product.id);
      } 
    },
    methods: {    // methods property for calling methods on components
      async addToCart() {   // use async methods for server requests
        await axios.post('/api/users/12345/cart', {   // two arguments for post methods, endpoint URL and request body as an object
          productId: this.$route.params.id,           // set productId equal to the param from the URL as seen in created method below. Review app.post method for this endpoint in server.js.
        });
        this.showSuccessMessage = true; // set flag to true
        setTimeout(() =>  {
          this.$router.push('/products');  // reroute to products page after 1.5 secs
        }, 1500);
      },
    },
    async created() {
      const { data: product } = await axios.get(`/api/products/${this.$route.params.id}`);  // get the id from the current page URL param, assign to variable 'product'
      this.product = product;

      const { data: cartItems } = await axios.get('/api/users/12345/cart');  // get cartItems for  ProductDetail page so app can use the list to check whether or not a specific product is already in cart
      this.cartItems = cartItems; 
    }
};
</script>

<style scoped>
  #page-wrap {
    margin-top: 16px;
    padding: 16px;
    max-width: 600px;
  }

  #img-wrap {
    text-align: center;
  }

  img {
    width: 400px;
  }

  #product-details {
    padding: 16px;
    position: relative;
  }

  #add-to-cart {
    width: 100%;
  }

  #price {
    position: absolute;
    top: 24px;
    right: 16px;
  }

  .green-button {
    background-color: green;
  }

  .grey-button {
    background-color: #888;
  }
</style>