<!-- in template, replace the previous loop through the cart items array with a simple call to ProductsList passing the cartItems list to the "products" prop -->
<!-- this "products" prop is then used in the ProductsList.vue page -->
<template>
  <div id="page-wrap">
    <h1>Shopping Cart</h1>
    <!-- use v-on to grab the event that has bubbled up from the ProductsListeItem and ProductsList components. -->
    <!-- Define new method for emit that gets called, in this case removeFromCart() with $event passed as the param -->
    <!-- removeFromCart() method added below to the export default component -->
    <ProductsList 
      :products="cartItems"
      v-on:remove-from-cart="removeFromCart($event)"
    />
    <h3 id="total-price">Total: ${{ totalPrice }}</h3>
    <button id="checkout-button">Proceed to Checkout</button>
  </div>
</template>

<script>

// Import ProductsList component
import axios from 'axios';
import ProductsList from '../components/ProductsList.vue';

// Export allows other components to import this component and display it
// Need to use a computed property to calculate total price dynamically of cart items as items are moved in and out of cart
// Totalprice function will re-calculate automatically each time something that affects the return value of the total items
// Uses reduce function cart items to help with calculating the total
// export ProductsList component
export default {
    name: 'CartPage',
    components: {
      ProductsList,
    },
    data() {
      return {
        cartItems: [],   // set to empty array because its a group of items
      }
    },
    computed: {
      totalPrice() {
        return this.cartItems.reduce(
          (sum, item) => sum + Number(item.price),
          0,
        );
      }
    },
    methods: {
      async removeFromCart(productId) {   // productId param bubbles up here from original event on ProductsListItem.vue
        const result = await axios.delete(`api/users/12345/cart/${productId}`); // delete method will return updated cartItems from user, see app.delete in server.js
        this.cartItems = result.data;
        console.log('Remove...'); 
      }
    },
    async created() {
      const result = await axios.get('/api/users/12345/cart');
      const cartItems = result.data;
      this.cartItems = cartItems; 
    }
};
</script>

<style scoped>
  h1 {
    border-bottom: 1px solid black;
    margin: 0;
    margin-top: 16px;
    padding: 16px;
  }

  #total-price {
    padding: 16px;
    text-align: right;
  }

  #checkout-button {
    width: 100%;
  }

  
</style>